
import Vnav from './home.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

export default{
components:{
Vnav,
LoadMore
},
data(){
return{
num:3,
isShow:false,
selectItem:[

],
isSelectShow:false,
selectVal:"",
lessonId:"",
theModel:"",
timeFrom:"",
timeTo:"",
content:"",
studentId:"",
scoreItem:[],
nodeItem:[
],
studentData:[],
}
},
mounted(){
if(this.$route.query.timeFrom){
this.dateUp = this.$route.query.timeFrom+":00";
}else{
this.dateUp = "";
}
if(this.$route.query.timeTo){
this.dateDown = this.$route.query.timeTo+":00";
}else{
this.dateDown = "";
}


this.gradeId = this.$route.query.gradeId;
this.lessonId = localStorage.getItem("courseId");
this.theModel = this.$route.query.theModel;
this.studentId = localStorage.getItem("studentId");
this.content = localStorage.getItem("studentName");
this.studentData = JSON.parse(localStorage.getItem("studentData"));

document.title=localStorage.getItem("studentName");
this.getData();
this.getNodeData();
},
methods:{
getNodeData(){
var vm = this;
vm.scoreItem = [];
this.$vux.loading.show({
text: 'Loading'
});
setTimeout(function (){
vm.$vux.loading.hide()
},1000);
$.ajax({
url:"http://120.78.214.169/study/node/queryByCondition",
type:"POST",
data:{

},
dataType:"json",
success:function(data){
vm.$vux.loading.hide();
var list = [];
data.forEach(function (v){
v.student = [];
});
vm.nodeItem = data;
data.forEach(function (v,i){
vm.getNodeScore(v.id,i);
});

}
})
},
getNodeScore(id){
var vm = this;

$.ajax({
url:"http://120.78.214.169/study/nodeScore/queryByCondition",
type:"POST",
data:{
lessonId:this.lessonId,
studentId:this.studentId,
nodeId:id,
timeFrom:this.dateUp,
timeTo:this.dateDown,
},
dataType:"json",
success:function(data){
data.forEach(function(v){
v.name = "";
vm.studentData.forEach(function (v1){
if(v.studentId == v1.id){
v.name = v1.name;
}
})
})
vm.nodeItem.forEach(function (v,i){
if(id == v.id){
vm.nodeItem[i].student = data;
}
})
}
})
},
getData(){
var vm = this;
vm.scoreItem = [];
this.$vux.loading.show({
text: 'Loading'
});
setTimeout(function (){
vm.$vux.loading.hide()
},1000);
$.ajax({
url:"http://120.78.214.169/study/score/queryByStudent",
type:"POST",
data:{
lessonId:this.lessonId,
theModel:this.theModel,


timeFrom:"",
timeTo:"",
studentId:this.studentId,
content:this.content
},
dataType:"json",
success:function(data){
var list = data.returnStr;
vm.$vux.loading.hide()
vm.scoreItem = list;
for (var key in data.times) {
vm.selectItem.push({
name:key
});
}
if(vm.selectItem.length !=0 ){
vm.selectVal = vm.selectItem[0].name;
}

}
})
},
showDialog(){
this.isShowDialog = true;
},

showSelect(){
this.isSelectShow = !this.isSelectShow;
if(this.isSelectShow){
$(".h-select-box img").css("-webkit-transform","rotateZ(180deg)");
}else{
$(".h-select-box img").css("-webkit-transform","rotateZ(0deg)");
}

},
hideSelect(index){
this.selectItem.forEach(function(v){
v.isSelect = false;
});
this.selectItem[index].isSelect = true;
this.selectVal = this.selectItem[index].name;
this.isSelectShow = false;
$(".h-select-box img").css("-webkit-transform","rotateZ(0deg)")
},
goResult(content){
localStorage.setItem("knowledgeName",content);
this.$router.push({path:"/resultStati",query:{gradeId:this.gradeId,lessonId:this.lessonId,theModel:this.theModel,timeFrom:this.dateUp,timeTo:this.dateDown}})
},

getNav(msg){
console.log(msg)
if(msg.status == 1){//1
this.isShow = false;
}
},
showNav(){
this.isShow = true;
},
hide(){
this.isShow = false;
}
},

}



