
import Vnav from './home.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import echarts from 'echarts'
export default{
components:{
Vnav,
Datetime,
Group
},
data(){
return{
num:3,
isShow:false,
visibility:false,
visibility1:false,
dateUp:"",
dateDown:"",
option : {
background:"#ffffff",
title : {
text: '',
x:'center'
},
tooltip : {
trigger: 'item',

},

series : [
{
name: '',
type: 'pie',
radius : '55%',
center: ['50%', '60%'],
label: {
normal: {
show: true,
formatter: '{b}: {c}'
}
},
data:[
{value:335, name:''},
{value:310, name:''},
{value:234, name:''},
],
itemStyle: {
emphasis: {
shadowBlur: 10,
shadowOffsetX: 0,
shadowColor: 'rgba(0, 0, 0, 0.5)'
}
}
}
]
},
scoreItem:[
],
gradeId:"",
lessonId:"",
theModel:"",
timeFrom:"",
timeTo:"",
content:"",
}
},
created(){
this.dateUp = this.$route.query.timeFrom;
this.dateDown = this.$route.query.timeTo;
},
mounted(){

this.gradeId = this.$route.query.gradeId;
this.lessonId = this.$route.query.lessonId;
this.theModel = this.$route.query.theModel;

this.content = localStorage.getItem("knowledgeName");
document.title=this.content;
this.getData();
},
computed:{
habs(){
if(!this.isShow&&!this.visibility&&!this.visibility1){
return true;
}else{
return false;
}
},
hfix(){
if(this.isShow||this.visibility||this.visibility1){
return true;
}else{
return false;
}
}
},
methods:{
getData(){
var vm = this;
this.$vux.loading.show({
text: 'Loading'
});
setTimeout(function (){
vm.$vux.loading.hide()
},1000);
$.ajax({
url:"http://120.78.214.169/study/score/queryByKnowledge",
type:"POST",
data:{
gradeId: this.gradeId,
lessonId:this.lessonId,
theModel:this.theModel,
timeFrom:this.dateUp+":00",
timeTo:this.dateDown+":00",


content:this.content
},
dataType:"json",
success:function(data){
var list = data;
vm.$vux.loading.hide()
vm.scoreItem = list;
}
})
},
showPlugin () {
this.visibility  = true;
},
showPlugin1 () {
this.visibility1  = true;
},
goPersonal(name,id){
localStorage.setItem("studentName",name);
localStorage.setItem("studentId",id);
this.$router.push({path:"/personalData",query:{gradeId:this.gradeId,lessonId:this.lessonId,theModel:this.theModel,timeFrom:this.dateUp,timeTo:this.dateDown}});
},
getNav(msg){
if(msg.status == 1){//1
this.isShow = false;
}
},
showNav(){
this.isShow = true;
},
hide(){
this.isShow = false;
},
drawGraph(id) {

this.chart = echarts.init(document.getElementById(id))


var that = this


this.chart.setOption(this.option)
},
},

}



