
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default{
directives: {
TransferDom
},
components:{
XDialog,
},
props:["numIndex"],
data(){
return{
showMark:false,
classItem:[
],
courseItem:[

],
scoreItem:[
{
name:"",
id:"00",
isActive:false
},
{
name:"",
id:"01",
isActive:false
},
{
name:"",
id:"10",
isActive:false
},
{
name:"",
id:"11",
isActive:false
},





],
studentItem:[
]
}
},
mounted(){
setTimeout(function(){
$(".h-tab .h-tab-btn").css("-webkit-transform","rotateZ(180deg)");
},0);
this.getStudent();
this.getClass();
this.getCourse();
},

methods:{
getStudent(){
var vm = this;
this.$vux.loading.show({
text: 'Loading'
});
setTimeout(function (){
vm.$vux.loading.hide()
},1000);
$.ajax({
url:"http://120.78.214.169/study/student/query",
type:"POST",
data:{
pageSize: 9999999,
currentPageIndex:1
},
dataType:"json",
success:function(data){
var list = data.datas;
localStorage.setItem("studentData",JSON.stringify(list));
vm.$vux.loading.hide()
if(list.length >0){
localStorage.setItem("studentName",list[0].name);
}
vm.studentItem = list;
}
})
},
getClass(){
var vm = this;

$.ajax({
url:"http://120.78.214.169/study/grade/query",
type:"POST",
data:{
pageSize: 9999999,
currentPageIndex:1
},
dataType:"json",
success:function(data){
var list = data.datas;
list.forEach(function (v){
v.isActive = false;
});
if(list.length >0){
list[0].isActive = true;
localStorage.setItem("className",list[0].name);
localStorage.setItem("classId",list[0].id);
}
vm.classItem = list;
}
})
},
getCourse(){
var vm = this;

$.ajax({
url:"http://120.78.214.169/study/lesson/queryByCondition",
type:"POST",
data:{
},
dataType:"json",
success:function(data){
var list = data;
list.forEach(function (v){
v.isActive = false;
});
if(list.length >0){
list[0].isActive = true;
localStorage.setItem("courseName",list[0].name);
localStorage.setItem("courseId",list[0].id);
}
vm.courseItem = list;
}
})
},

chooseClass(index){
this.classItem.forEach(function(v){
v.isActive = false;
});
this.classItem[index].isActive = true;
localStorage.setItem("className",this.classItem[index].name);
localStorage.setItem("classId",this.classItem[index].name);
},
chooseCourse(index){
this.courseItem.forEach(function(v){
v.isActive = false;
});
this.courseItem[index].isActive = true;
localStorage.setItem("courseName",this.courseItem[index].name);
localStorage.setItem("courseId",this.courseItem[index].id);
},
chooseScore(index){

this.scoreItem.forEach(function(v){
v.isActive = false;
});
this.scoreItem[index].isActive = true;
localStorage.setItem("scoreName",this.scoreItem[index].name);
if(index == 0){
this.$router.push({path:"/appoTiptop"});
this.$emit('get-nav', { status:1});
}
if(index == 1){
this.$router.push({path:"/appoAverage"});
this.$emit('get-nav', { status:1});
}
if(index == 2){
this.$router.push({path:"/normalTiptop"});
this.$emit('get-nav', { status:1});
}
if(index == 3){
this.$router.push({path:"/normalAverage"});
this.$emit('get-nav', { status:1});
}
if(index == 4){
this.showMark = true;
}

},
chooseStudent(index){
localStorage.setItem("studentName",this.studentItem[index].name);
localStorage.setItem("studentId",this.studentItem[index].id);
this.$router.push({path:"/personalData"});
this.$emit('get-nav', { status:1});
location.reload();
},
clickBtn(){
this.$emit('get-nav', { status:1})
}
}
}



