import Vue from 'vue'
import VueRouter from 'vue-router'



import HelloWorld from '@/components/HelloWorld'
import login from '@/components/h5/login'
import home from '@/components/h5/home'
import appoTiptop from '@/components/h5/appo-tiptop'
import appoAverage from '@/components/h5/appo-average'

import normalTiptop from '@/components/h5/normal-tiptop'
import normalAverage from '@/components/h5/normal-average'

import resultStati from '@/components/h5/result-stati'
import personalData  from '@/components/h5/personal-data'

Vue.use(VueRouter)


const routes = [
    {
      path: '/HelloWorld',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/',
      name: 'login',
      component: login
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/appoTiptop',
      name: 'appoTiptop',
      component: appoTiptop
    },
    {
      path: '/appoAverage',
      name: 'appoAverage',
      component: appoAverage
    },
    {
      path: '/normalTiptop',
      name: 'normalTiptop',
      component: normalTiptop
    },
    {
      path: '/normalAverage',
      name: 'normalAverage',
      component: normalAverage
    },
    {
      path: '/resultStati',
      name: 'resultStati',
      component: resultStati
    },
    {
      path: '/personalData',
      name: 'personalData',
      component: personalData
    }
]

const router = new VueRouter({
  	mode: 'history',
	base: __dirname,
	routes
})
export default router
