
import Vnav from './home.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import XDialog from 'vux/src/components/x-dialog/index.vue'

import LoadMore from 'vux/src/components/load-more/index.vue'

import echarts from 'echarts'
export default{
directives: {
TransferDom
},
components:{
Vnav,
Datetime,
Group,
XDialog,
LoadMore
},

data(){
return{
num:3,
isShow:false,
visibility:false,
visibility1:false,
dateUp:"",
dateDown:"",
isShowDialog:false,
scoreItem:[
],
gradeId:"",
lessonId:"",
theModel:"11",
nodeItem:[
],
studentData:[],
option : {
background:"#ffffff",
title : {
text: '',
x:'center'
},
tooltip : {
trigger: 'item',

},

series : [
{
name: '',
type: 'pie',
radius : '55%',
center: ['50%', '60%'],
label: {
normal: {
show: true,
formatter: '{b}: {c}'
}
},
data:[
{value:335, name:''},
{value:310, name:''},
{value:234, name:''},
],
itemStyle: {
emphasis: {
shadowBlur: 10,
shadowOffsetX: 0,
shadowColor: 'rgba(0, 0, 0, 0.5)'
}
}
}
]
},
}
},
created (){
this.dateDown = this.timeFn();
this.dateUp = this.timeFn1();
},
mounted(){
this.gradeId  =  localStorage.getItem("classId");
this.lessonId  =  localStorage.getItem("courseId");
var className = localStorage.getItem("className");
var courseName = localStorage.getItem("courseName");
var scoreName = localStorage.getItem("scoreName");
this.studentData = JSON.parse(localStorage.getItem("studentData"));
document.title=className+courseName+scoreName;
this.getData();
this.getNodeData();
},
computed:{

habs(){
if(!this.isShow&&!this.visibility&&!this.visibility1){
return true;
}else{
return false;
}
},
hfix(){
if(this.isShow||this.visibility||this.visibility1){
return true;
}else{
return false;
}
}
},
methods:{
getNodeData(){
var vm = this;
vm.scoreItem = [];
this.$vux.loading.show({
text: 'Loading'
});
setTimeout(function (){
vm.$vux.loading.hide()
},1000);
$.ajax({
url:"http://120.78.214.169/study/node/queryByCondition",
type:"POST",
data:{

},
dataType:"json",
success:function(data){
vm.$vux.loading.hide();
var list = [];
data.forEach(function (v){
v.student = [];
v.option = {
background:'#ffffff',
title : {text: '',x:'center'},
tooltip : {trigger: 'item', },
series : [
{
name: '',
type: 'pie',
radius : '55%',
center: ['50%', '60%'],
label: {
normal: {
show: true,
formatter: '{b}: {c}'
},
},
data:[
{value:335,name:""},
{value:310, name:''},
{value:234, name:''},
],
itemStyle: {
emphasis: {
shadowBlur: 10,
shadowOffsetX: 0,
shadowColor: 'rgba(0, 0, 0, 0.5)'
}
}
}]
}
});
vm.nodeItem = data;
data.forEach(function (v,i){
vm.getNodeScore(v.id,i);
});
}
})
},
getNodeScore(id,index){
var vm = this;







$.ajax({
url:"http://120.78.214.169/study/nodeScore/queryByCondition",
type:"POST",
data:{
lessonId:this.lessonId,
studentId:"",
nodeId:id,
timeFrom:this.dateUp+":00",
timeTo:this.dateDown+":00",
mode:""
},
dataType:"json",
success:function(data){
data.forEach(function(v){
v.name = "";
vm.studentData.forEach(function (v1){
if(v.studentId == v1.id){
v.name = v1.name;
}
})
})
vm.nodeItem.forEach(function (v,i){
if(id == v.id){
vm.nodeItem[i].student = data;
}
})

}
})
},
getData(){
var vm = this;
vm.scoreItem = [];
this.$vux.loading.show({
text: 'Loading'
});
setTimeout(function (){
vm.$vux.loading.hide()
},1000);
$.ajax({
url:"http://120.78.214.169/study/score/query",
type:"POST",
data:{
gradeId: this.gradeId,
lessonId:this.lessonId,
theModel:this.theModel,
timeFrom:this.dateUp+":00",
timeTo:this.dateDown+":00",
},
dataType:"json",
success:function(data){
vm.$vux.loading.hide()
var list = data;
vm.scoreItem = list;
}
})
},
showPlugin () {
this.visibility  = true;
},
showPlugin1 () {
this.visibility1  = true;
},
showDialog(){
this.isShowDialog = true;
},
goResult(content){
localStorage.setItem("knowledgeName",content);
this.$router.push({path:"/resultStati",query:{gradeId:this.gradeId,lessonId:this.lessonId,theModel:this.theModel,timeFrom:this.dateUp,timeTo:this.dateDown}})
},
getNav(msg){
if(msg.status == 1){//1
this.isShow = false;
}
},
showNav(){
this.isShow = true;
},
hide(){
this.isShow = false;
},
timeFn(){
var nowDate = new Date();
var year= nowDate.getFullYear();
var month = nowDate.getMonth()+1;
var today = nowDate.getDate();
var hours = nowDate.getHours();
var minutes = nowDate.getMinutes();
var seconds = nowDate.getSeconds();

if(month >= 1 && month <=9){
month = "0" + month;
}
if(today >= 1 && today <=9){
today = "0" + today;
}
if(hours >= 0 && hours <=9){
hours = "0" + hours;
}
if(minutes >= 0 && minutes <=9){
minutes = "0" + minutes;
}
if(seconds >= 0 && seconds <=9){
seconds = "0" + seconds;
}

var currentdate = year + "-" + month + "-" + today + " " + hours + ":" +minutes;
return currentdate;
},
timeFn1(){
var t = new Date().getTime()-259200000;

var nowDate = new Date(t);
var year= nowDate.getFullYear();
var month = nowDate.getMonth()+1;
var today = nowDate.getDate();
var hours = nowDate.getHours();
var minutes = nowDate.getMinutes();
var seconds = nowDate.getSeconds();

if(month >= 1 && month <=9){
month = "0" + month;
}
if(today >= 1 && today <=9){
today = "0" + today;
}
if(hours >= 0 && hours <=9){
hours = "0" + hours;
}
if(minutes >= 0 && minutes <=9){
minutes = "0" + minutes;
}
if(seconds >= 0 && seconds <=9){
seconds = "0" + seconds;
}
var currentdate = year + "-" + month + "-" + today + " " + hours + ":" +minutes;
return currentdate;
},
drawGraph(option,ele) {

this.chart = echarts.init(ele)


var that = this


this.chart.setOption(option)
},
},

}



